.notFound {
    font-family: 'Montserrat', sans-serif;
    font-weight: 800;  
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80vh;
    margin: 0;
    letter-spacing: -1%;
    color: #0F0F11;

    &-title {
      margin-bottom: 10px;
      font-size: 48px;
      text-align: center;
      line-height: 56px;
    }
  }
  
  @media (max-width: 320px) {
    .notFound-title {
        font-size: 32px;
        line-height: 41px;
    }
  }